import { Middleware } from "redux";
import { LOCATION_CHANGE } from "connected-react-router";

import { FORCE_SMOOTH_SCROLL } from "state/actionTypes";
import smoothScroll from "utils/smoothScroll";

let initialPageScroll = true;

function scroll() {
  if (initialPageScroll) {
    initialPageScroll = false;
    return;
  }

  const { hash } = window.location;
  if (!hash && !initialPageScroll) {
    return window.scrollTo(0, 0);
  }

  const el = document.getElementById(hash.substring(1)) as HTMLElement;
  if (!el) {
    return;
  }

  let headerHeight = 0;
  const header = document.getElementById("main-header") as HTMLElement;
  if (header) {
    headerHeight = header.offsetHeight;
  }
  smoothScroll(el.offsetTop - headerHeight);
}

export const scrollingMiddleware: Middleware = store => next => action => {
  const { type, payload } = action;
  if (type === LOCATION_CHANGE || type === FORCE_SMOOTH_SCROLL) {
    window.requestAnimationFrame(scroll);
  }

  return next(action);
};
